import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import AddTap from './add-tap'
import EditTap from './edit-tap'
import TapsList from './taps-list'

const PublicTap = ({match, systemData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match}`} to={`${match}/list`} />
			<Route path={`${match}/add-tap`} render={ () => (<AddTap systemData={systemData}/>) } />
			<Route path={`${match}/edit-tap/:id`} component={EditTap} />			
			<Route path={`${match}/list`} render={ () => (<TapsList systemData={systemData}/>) } />
		</Switch>
	)
}

export default PublicTap

