import React, {useState} from 'react'
import moment from "moment";
import { Input, Row, Col, Card, Form, DatePicker, Upload, InputNumber, message, Select } from 'antd';
import LocationData from "assets/data/district.json"
import { useCollection } from "react-firebase-hooks/firestore";
import { DataService } from "services/settings.service";

const { Dragger } = Upload;
const { Option } = Select;

const rules = {
	name: [
		{
			required: true,
			message: 'Please enter member name',
		}
	],
	idNumber: [
		{
			required: true,
			message: 'Please enter id number'
		}
	],
	phoneNumber: [
		{
			required: true,
			message: 'Please enter phone number',
		}
	],
	district: [
		{
			required: true,
			message: 'Select district',
		}
	],
	sector: [
		{
			required: true,
			message: 'Select sector',
		}
	],
	cell: [
		{
			required: true,
			message: 'Select cell',
		}
	],
	village: [
		{
			required: true,
			message: 'Select Village',
		}
	],
	site: [
		{
			required: true,
			message: 'Select a site',
		}
	],
	meter: [
		{
			required: true,
			message: 'Water meter number',
		}
	],
	dateJoined: [
		{
			required: true,
			message: 'Invalid Date',
		}
	]
}

const GeneralField = props => {
	const [provinceData] = useState(LocationData);
	const [settings, settingsLoading] = useCollection(DataService.getAll());
	const [setting, setSetting] = useState([]);
	const [showLoading, setShowLoading] = useState(true);
	const {sites, sitesLoading} = props;

	if(!settingsLoading){
		let allRecords = [];
	  	let lastSetting = '';
		settings.docs.map((doc) => {
		allRecords = {
		  ...allRecords,
		  [doc.data()._id] : {'value': doc.data().value},
		}
		lastSetting = doc.data()._id;
		return true;
		})
		if(lastSetting && (showLoading || !setting[lastSetting])){
			setSetting(allRecords)
			setShowLoading(false);
		}
	}

	const [state, setState] = useState({
		sectors: [],
		cells: [],
		villages: [],
		sectorId : [],
		cellId : [],
		villageId: []
	});

	const handleDistrictChange = value => {
		setState({
		sectors: provinceData.districts[value].sectors.all,
		sectorId : provinceData.districts[value].sectors,
		cells: [],
		villages: [],
		cellId : [],
		villageId: []
		});
		props.handleStates({
			sectorName : '',
			cellName : '',
			villageName: '',
		})
	};

	const handleSectorChange = value => {
		setState({
		...state,
		cells: state.sectorId[value].cells.all,
		cellId: state.sectorId[value].cells,
		villages: [],
		villageId: []
		});
		props.handleStates({
			sectorName : state.sectorId[value].name,
			cellName : '',
			villageName: ''
		})
	};

	const handleCellChange = value => {
		setState({
		...state,
		villages: state.cellId[value].villages.all,
		villageId: state.cellId[value].villages,
		});
		props.handleStates({
			...props.initialState,
			cellName : state.cellId[value].name,
			villageName: ''
		})
	};

	const handleVillageChange = value => {
		props.handleStates({
			...props.initialState,
			villageName : state.villageId[value].name,
		});
	};
 
	const disabledDate = (current) => {
		let response = false;
		if(current && (current > moment().endOf("day"))) response = true;
		return current && response;
	}

	return (
		
	<Row gutter={16}>
		
		<Col span={24}>
			<Card>
			<Row gutter={16}>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="name" label="Manager name" rules={rules.name}>
							<Input placeholder="Manager Name" />
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item 
							name="phoneNumber" 
							label={"Phone Number"}
							rules={rules.phoneNumber}>
							<InputNumber
								className="w-100"
								formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="site" label="Site" rules={rules.site}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select a site"
									optionFilterProp="children"
								>
								{!sitesLoading && sites.docs.map((site, index) => (
									<Option key={index} value={site.data()._id}>
										{site.data().siteId+' - '+site.data().name}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="meter" label="Meter Number" rules={rules.meter}>
							<InputNumber className="w-100" placeholder="Water Meter" />
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item rules={rules.dateJoined} label="Date Created" name="dateJoined">
							<DatePicker className="w-100" format="DD-MM-YYYY" disabledDate={disabledDate}/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="district" label="District" rules={rules.district}>
							<Select
								className="w-100"
								placeholder="-"
								showSearch
								optionFilterProp="children"
								onChange={handleDistrictChange}
							>
								{provinceData.districts.all.map((district, index) => (
								<Option key={index} value={district.id}>{district.name}</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="sector" label="Sector" rules={rules.sector}>
							<Select
								className="w-100"
								placeholder="-"
								showSearch
								optionFilterProp="children"
								onChange={handleSectorChange}
								>
								{state.sectors.map((sector, index) => (
									<Option key={index} value={sector.id}>{sector.name}</Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="cell" label="Cell" rules={rules.cell}>
							<Select
								className="w-100"
								placeholder="-"
								showSearch
								optionFilterProp="children"
								onChange={handleCellChange}
							>
								{state.cells.map((cell, index) => (
									<Option key={index} value={cell.id}>{cell.name}</Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="village" label="Village" rules={rules.village}>
							<Select className="w-100" 
									placeholder="-" 
									showSearch
									optionFilterProp="children"
									onChange={handleVillageChange}
								>
							{
								state.villages.map((data,index) => (
									<Option key={index} value={data.id}>{data.name}</Option>
									))
							}
							</Select>
							
						</Form.Item>
					</Col>
					
				</Row>
				
			</Card>
		</Col>
	</Row>
)}

export default GeneralField
