import React from 'react'
import { Card, Table, Button, message, Menu, Modal, Avatar } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, LoadingOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';
import Flex from 'components/shared-components/Flex'
import { useHistory } from "react-router-dom";
import { DataService } from "services/taps.service";
import { Typography } from "antd";
import utils from 'utils';

const { Title } = Typography;
const { confirm } = Modal;

const TapsList = ({systemData}) => {
	let history = useHistory();
	const { avatarColors, taps, tapsLoading, site, sitsLoading, unpaidBalance } = systemData;
	const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => showConfirm(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);
	
	const addProduct = () => {
		history.push(`/app/apps/publicTap/add-tap`)
	}
	
	const viewDetails = row => {
		history.push(`/app/apps/publicTap/edit-tap/${row._id}`)
	}
	
	const deleteRow = row => {
		DataService.remove(row._id)
		message.success({ content: `Public Tap ${row.name} deleted `, duration: 3 });
	}

	function showConfirm(row) {
		confirm({
		  title: 'Do you want to delete this public tap?',
		  onOk() {
			deleteRow(row)
		  },
		  onCancel() {},
		});
	  }

	const tableColumns = [
		{
			title: 'ID',
			dataIndex: 'id',
			render: (_, record) => (
				<div style={{cursor:'pointer'}}>
					{!sitsLoading ? (site[record.data().site] ? site[record.data().site].siteId : record.data().backupSiteSiteId)+''+record.data().tapId:(<LoadingOutlined/>)}
				</div>)
		},
		{
			title: 'Manager',
			dataIndex: 'manager',
			render: (_,record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={30} type="square" src={
						<Avatar size={30} className="font-size-sm" style={{backgroundColor: avatarColors[Math.floor(Math.random() * avatarColors.length) - 1]}}>
						  {utils.getNameInitial(record.data().name)}
						</Avatar>} name={record.data().name} subTitle={'0'+record.data().phoneNumber}/>
				</div>
			  )
		},
		{
			title: 'Water Meter',
			dataIndex: 'meter',
			render: (_, record) => record.data().meter
		},
		{
			title: 'Site',
			dataIndex: 'site',
			render: (_, record) => !sitsLoading ? (site[record.data().site] ? site[record.data().site].name : record.data().backupSiteName):(<LoadingOutlined/>)
		},
		{
			title: 'Balance',
			dataIndex: 'balance',
			render: (_, record) => (
						<NumberFormat displayType={'text'} 
							value={unpaidBalance(record)}
							thousandSeparator={true}/>
					)
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
	];

	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Public Taps</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={addProduct} size="small" type="primary" icon={<PlusCircleOutlined />} block>New Tap</Button>
				</div>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!tapsLoading ? taps.docs : []} 
					loading={tapsLoading}
					rowKey='_id' 
				/>
			</div>
		</Card>
	)
}

export default TapsList
